.main-header {
    background-color: $component-content-background;
    border: 2px solid $component-content-border;
    color: $component-content-text;
    display: flex;
    margin: 10px;

    &--accent {
        background-color: $component-content-accent-background;
        color: $component-content-accent-text;
        display: flex;
        margin: 10px;
    }

    &--positive {
        background-color: $component-content-positive-background;
        color: $component-content-positive-text;
        display: flex;
    }

    &--error {
        background-color: $component-content-negative-background;
        color: $component-content-negative-text;
        display: flex;
    }

    // import nested elements
    @import "branding";
    @import "content";
    @import "callout";
}
