@import "../../mixins/clickable";

//button-base takes a string that is used by the clickable mixin
@mixin button-base($feedback-type: "neutral") {
    //clickable mixin and feedback type determine styles
    @include clickable($feedback-type);
    @include rhythmBorder($horizontal-multiplier: 2);   
    
    //sets standard button styles
    border: none;
    border-radius: 5px;        
    display: inline-block;
    text-transform: uppercase;
}