//Uses button base mixin from the button/_base.scss file
@import "./base";

.button {
    @include button-base();

    &--primary {
        @include button-base(primary);
    }

    &--disabled {
        @include button-base(disabled);
    }

    &--positive {
        @include button-base(positive);
    }

    &--negative {
        @include button-base(negative);
    }
}