.main-nav {
    height: 100%;
    display: flex;
    align-items: center;

    ul {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    li {
        display: inline-block;
        padding: 20px;
        border-right: 1px solid $component-main-nav-links-border;

        &:last-of-type {
            border-right: none;
        }

        a {
            color: $component-main-nav-links;
            text-decoration: none;
        }
    }

    &--accent {
        display: flex;
        align-items: center;
        height: 100%;
        

        ul {
            list-style: none;
            margin: 0;
            padding: 0;
        }

        li {
            display: inline-block;
            padding: 20px;
            border-right: 1px solid $component-main-nav-links-border-inverse;

            &:last-of-type {
                border-right: none;
            }

            a {
                color: $component-main-nav-links-inverse;
                text-decoration: none;
            }
        }
    }
}
